<template>
  <div class="section">
    <v-header></v-header>
    <div class="section_box">
      <div class="left_aside">
         <div @click="totab(0,'首页')" :class="tabIndex==0?'select_meun':''">
           <img src="../../assets/img/shouye.png" alt=""><span>首页</span>
         </div>
        <div @click="totab(1,'任务工单')" :class="tabIndex==1?'select_meun':''">
          <img src="../../assets/img/renwugondgan.png" alt=""><span>任务工单</span>
        </div>
        <div @click="totab(2, '消息通知')" :class="tabIndex==2?'select_meun':''">
          <img src="../../assets/img/xiaoxiguanli.png" alt=""><span>消息通知</span>
        </div>
      </div>
      <!--右侧子页面-->
      <div class="right-aside">
        <router-view/>
      </div>
    </div>
  </div>
</template>
<script>
  import vHeader from '../../components/header'
  export default {
    components:{
      vHeader
    },
    data () {
      return {
        tabIndex:0
      }
    },
    methods:{
      totab(index,pathName){
        this.tabIndex = index
        switch (pathName) {
          case '首页':
              this.$router.push('/detection/actuatingIndex/actuatingHomePage')
              return
          case '任务工单':
            this.$router.push('/detection/actuatingIndex/taskSheet')
            return
          case '消息通知':
            this.$router.push('/detection/actuatingIndex/messageNotice')
            return
          default:
            return
        }
      }
    },
    mounted () {
      switch (this.$route.path) {
        case '/detection/actuatingIndex/actuatingHomePage':
          this.tabIndex = 0
              return false
        case '/detection/actuatingIndex/taskSheet':
          this.tabIndex = 1
          return false
        case '/detection/actuatingIndex/messageNotice':
          this.tabIndex = 2
          return false
        default:
          return false
      }
    }
  }
</script>
<style lang="less" scoped>
  .select_meun{
    background: #157AF7;
  }
  .section{
    display: flex;
    flex-direction: column;
    height: 100vh;
    overflow: hidden;
      .section_box{
        display: flex;
        flex-direction: row;
        height:90.5vh;
        flex: 1;
        .left_aside{
          width:320px;
          background: #112435;
          height:100%;
          padding-top:20px;
          div{
            height:50px;
            line-height: 50px;
            color: #ffffff;
            padding-left: 92px;
            display: flex;
            align-items: center;
            cursor: pointer;
            span{
              margin-left: 23px;
            }
          }
        }
        .right-aside{
          background: #ffffff;
          flex: 1;
          height:100%;
          overflow-y: auto;
        }
      }
  }

</style>



