<template>
  <div class="section">
    <v-header></v-header>
    <div class="section_box">
      <div class="left_aside">
        <div @click="totab(0,'首页')" :class="tabIndex==0?'select_meun':''">
          <img src="../../assets/img/shouye.png" alt=""><span>首页</span>
        </div>
        <div @click="totab(1,'检测审核')" :class="tabIndex==1?'select_meun':''">
          <img src="../../assets/img/jianceshenhe.png" alt=""><span>检测审核</span>
        </div>
        <div @click="totab(2, '过程监督')" :class="tabIndex==2?'select_meun':''">
          <img src="../../assets/img/guochengjianguan.png" alt=""><span>过程监督</span>
        </div>
        <div @click="totab(3, '消息管理')" :class="tabIndex==3?'select_meun':''">
          <img src="../../assets/img/xiaoxiguanli.png" alt=""><span>消息管理</span>
        </div>
      </div>
      <!--右侧子页面-->
      <div class="right-aside">
        <router-view/>
      </div>
    </div>
  </div>
</template>
<script>
  import vHeader from '../../components/header'
  export default {
    components:{
      vHeader
    },
    data () {
      return {
        tabIndex:0
      }
    },
    methods:{
      totab(index,pathName){
        this.tabIndex = index
        switch (pathName) {
          case '首页':
            this.$router.replace('/detection/adminIndex/adminHomePage')
            return
          case '检测审核':
            this.$router.replace('/detection/adminIndex/testAuditing')
            return
          case '过程监督':
            this.$router.replace('/detection/adminIndex/processSupervision')
            return
          case '消息管理':
            this.$router.replace('/detection/adminIndex/messageManage')
            return
          default:
            return
        }
      }
    },
    mounted () {
      switch (this.$route.path) {
        case '/detection/adminIndex/adminHomePage':
          this.tabIndex = 0
          return false
        case '/detection/adminIndex/testAuditing':
          this.tabIndex = 1
          return false
        case '/detection/adminIndex/processSupervision':
          this.tabIndex = 2
          return false
        case '/detection/adminIndex/messageManage':
          this.tabIndex = 3
          return false
        default:
          return false
      }
    }
  }
</script>
<style lang="less" scoped>
  .select_meun{
    background: #157AF7;
  }
  .section{
    display: flex;
    flex-direction: column;
    height: 100vh;
    overflow: hidden;
    .section_box{
      display: flex;
      flex-direction: row;
      height:90.5vh;
      flex:1;
      .left_aside{
        width:320px;
        background: #112435;
        height:100%;
        padding-top:20px;
        div{
          height:50px;
          line-height: 50px;
          color: #ffffff;
          padding-left: 92px;
          display: flex;
          align-items: center;
          cursor: pointer;
          span{
            margin-left: 23px;
          }
        }
      }
      .right-aside{
        background: #ffffff;
        flex: 1;
        height:100%;
        overflow-y: auto;
      }
    }
  }

</style>



