<template>
  <div class="commom_style">
    <firstTitle name="过程监督"></firstTitle>
    <div class="input_box">
      <div class="search_box">
        <div class="commom_input">
          <label>工单编号:</label>
          <Input placeholder="请输入工单编号" style="margin-left: 10px;width:160px" v-model="postData.number"/>
        </div>
        <div class="commom_input">
          <label>负责专员:</label>
          <Input placeholder="请输入负责专员" style="margin-left: 10px;width:160px" v-model="postData.operator"/>
        </div>
        <div class="commom_input">
          <label>工单状态:</label>
          <Select placeholder="请选择工单状态" style="margin-left: 10px;width:160px" v-model="postData.status" >
            <Option v-for="(i,j) in orderStatusList" :value="i.value">{{i.text}}</Option>
          </Select>
        </div>
        <div class="commom_input" style="margin-right: 20px;">
          <label>修改日期:</label>
          <DatePicker type="datetime" placeholder="起始时间"
                      style="width: 180px;margin-left: 10px;"
                      v-model="postData.updateTimeStart"
                      @on-change="selectStart"></DatePicker>
          &nbsp; — &nbsp;
          <DatePicker type="datetime"
                      placeholder="结束时间"
                      style="width: 180px"
                      @on-change="selectEnd"
                      v-model="postData.updateTimeEnd"></DatePicker>
        </div>
      </div>
      <div class="search_btn">
        <Button type="primary" style="margin-left: 20px" @click="findProcessSupervision()">搜索</Button>
        <Button type="primary" style="margin-left: 20px" @click="resetData()">重置</Button>
      </div>
    </div>
    <Table stripe :columns="columns1" :data="data1">
      <template slot-scope="{ row, index }" slot="action">
        <span class="table_btn" @click="toConnect(row.id)">沟通监督</span>
        <span class="table_btn" @click="jumpNext(row.reportUrl)" v-if="row.status == 9">报告查看</span>
        <span class="table_btn" @click="reportDowm(row.id,row.reportName)" v-if="row.status == 9">报告下载</span>
        <span class="table_btn grey_btn" v-if="row.status != 9">报告查看</span>
        <span class="table_btn grey_btn" v-if="row.status != 9">报告下载</span>
        <span class="table_btn" @click="orderLook()">工单流转查看</span>
        <span class="table_btn">工单流转下载</span>
      </template>
    </Table>
    <splitPage :total="total" :page="postData.page"
               @psizeChange="psizeCha" @pageChange="pageCha"
               :rows="postData.rows"></splitPage>
    <commomComfirm ref="myComfirm" titleName="沟通监督" modalWidth="720">
      <div style="height:410px; overflow: auto; border: 1px solid #D8D8D8;padding:0 20px 20px;">
        <div class="message_box" v-for=" (item,index) in connectList">
          <div style="margin: 20px 0;font-size: 18px;">{{item.requestName}}:</div>
          <div class="img_file" v-if="item.imgUrl">
            <span>图片附件:</span>
            <span>
            <img :src="item.imgUrl" alt="">
          </span>
          </div>
          <div class="ques_detail">
            <span>问题描述:</span>
            <span>
            {{item.chat}}
          </span>
          </div>
          <span class="ask_time">提问时间：{{item.createTime}}</span>
        </div>
      </div>
      <div class="form_btn">
        <Button type="primary" style="background: #687D97;" class="back_btn" @click="$refs.myComfirm.modal = false">取消</Button>
        <Button type="primary" class="back_btn" @click="">保存</Button>
      </div>
    </commomComfirm>
    <commomComfirm ref="myOrderComfirm" titleName="工单流转" modalWidth="720">
      <div class="orderComfirm">
          <p>工单基本信息</p>
          <div class="orderInfo">
            <div><span>工单编号</span><span>123456</span></div> <div><span>其他附件</span><span>123456</span></div>
            <div><span>检测体附件</span><span>1.zip</span></div> <div><span>申请日期</span><span>2020</span></div>
          </div>
         <p>威胁扫描结果</p>
        <ul>
            <li v-for="i in 3">
              <div>
                <img style="margin-right: 16px" src="../../assets/img/cve.png" alt="">
                漏洞
              </div>
              <div> 检测完毕，没有发现恶意代码</div>
            </li>
        </ul>
        <p>审核过程详情</p>
        <div class="process_detaile">
          <div class="detail_list" v-for="i in 5">
            <div>
              <div>2020.04.11 08:23:27</div>
              <div style="margin-left: 20px;display: flex;flex-direction: column">
                <span style="font-size: 14px;">提交申请</span>
                <span style="font-size: 12px;color: #687D97;">驳回理由：</span></div>
            </div>
            <div>
              <span>操做人:</span>
              <span>张三三 <span style="color: #979797;">（审核管理员）</span></span>
            </div>
          </div>
        </div>
        <p>检测过程详情</p>
        <div class="process_detaile">
          <div class="detail_list" v-for="i in 5">
            <div>
              <div>2020.04.11 08:23:27</div>
              <div style="margin-left: 20px;display: flex;flex-direction: column">
                <span style="font-size: 14px;">提交申请</span>
                <span style="font-size: 12px;color: #687D97;">驳回理由：</span></div>
            </div>
            <div>
              <span>操做人:</span>
              <span>张三三 <span style="color: #979797;">（审核管理员）</span></span>
            </div>
          </div>
        </div>
        <div style="text-align: center;">
          <Button type="primary" style="width: 200px;margin:20px auto" @click="$refs.myOrderComfirm.modal = false">关闭</Button>
        </div>
      </div>

    </commomComfirm>

    <a :href="JumpUrl" ref="jumpA" style="display: none" target='_blank'></a>
  </div>
</template>
<script>
  import firstTitle from '../../components/firstTitle'
  import splitPage from '../../components/splitPage'
  import commomComfirm from '../../components/comomComfirm'
  export default {
    components:{firstTitle,splitPage,commomComfirm},
    mixins: [require('../../assets/js/common').commomMethods],
    data () {
      return {
        columns1: [
          {
            title: '序号',
            type: 'index',
            align:'center',
            width:70,
          },
          {
            title: '工单编号',
            key: 'number',
            align:'center',
            tooltip: true,
          },
          {
            title: '工单类型',
            key: 'typeName',
            align:'center',
            tooltip: true
          },
          {
            title: '负责专员',
            key: 'operater',
            align:'center',
            tooltip: true
          },
          {
            title: '工单状态',
            key: 'statusName',
            align:'center',
            tooltip: true
          },
          {
            title: '创建时间',
            key: 'createTime',
            align:'center',
            tooltip: true
          },
          {
            title: '操作',
            slot: 'action',
            align:'center',
            minWidth:80
          }
        ],
        data1: [],
        total:'',
        postData:{
          page:1,
          rows:10,
          number:'',
          operator:'',
          updateTimeStart:'',
          updateTimeEnd:'',
          status:''
        },
        connectData:{
          orderId:'',
          chat:'',
          imgName:'',
          files:''
        },
        orderStatusList:[],
        connectList:[],
        JumpUrl:'',
      }
    },
    methods:{
      reportDowm (id,fileName) {
        this.commomDwon(id,fileName,'reportName')
      },
      // 查看报告
      jumpNext (url) {
        if(url){
          this.JumpUrl = url
         let _this = this
          setTimeout(function () {
            _this.$refs.jumpA.click()
          }, 500)
        }else {
          this.$Message.error('暂无报告')
        }
      },
      // 工单流转查看
      orderLook () {
        this.$refs.myOrderComfirm.modal = true
      },
      resetData () {
        this.postData.number = ''
        this.postData.operator = ''
        this.postData.updateTimeStart = ''
        this.postData.updateTimeEnd = ''
        this.postData.status = ''
      },
      selectEnd (val){
        this.postData.updateTimeEnd = val
      },
      selectStart (val){
        this.postData.updateTimeStart = val
      },
      psizeCha(val) {
        this.postData.rows = val
        this.findProcessSupervision()
      },
      pageCha(val) {
        this.postData.page = val
        this.findProcessSupervision()
      },
      findProcessSupervision () {
        this.$req().post(this.Api().findProcessSupervision,this.postData, function(res) {
          if(res.data.code == 200) {
            this.data1 = res.data.data.list
            this.total = res.data.data.total
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      },
      // 沟通留言
      toConnect (orderId) {
        this.$refs.myComfirm.modal = true
        this.getConnectsList(orderId)
      },
      // 沟通列表
      getConnectsList (orderId) {
        this.$req().get(this.Api().getConnectsList+'?orderId='+ orderId, function(res) {
          if(res.data.code == 200){
            this.connectList = res.data.data
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      },
      //工单状态
      getOrderStatusList(){
        this.$req().get(this.Api().getOrderStatusList+'?parentId='+ 660, function(res) {
          if(res.data.code == 200){
            this.orderStatusList = res.data.data
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      }
    },
    mounted () {
      this.findProcessSupervision()
      this.getOrderStatusList()
    }
  }
</script>
<style lang="less" scoped>
  @import "../../assets/css/commom";
  .form_btn{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width:500px;
    margin:60px auto 0;
    text-align: center;
  }
  .input_box{
    .comborder;
    display: flex;
    flex-direction: row;
    margin-bottom: 20px;
    padding:10px 0;
    .search_box{
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      padding-top:10px;
    }
    .search_btn{
      min-width:180px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      border-left: 1px solid #D8D8D8;
      padding:0 20px 0 0px;
      margin-left: 20px;
    }
  }
  .message_box{
    width:100%;
    border-top: 1px solid #D8D8D8;
    position: relative;
    &:first-child{
      border-top: none !important;
    }
    .ask_time{
      position: absolute;
      right:20px;
      bottom:-10px;
      font-size: 14px;
      color: #999999;
    }
    .img_file{
      display: flex;
      flex-direction: row;
      span:first-child{
        min-width: 70px;
        font-size: 14px;
        color: #333333;
      }
      span:last-child{
        width:100%;
        height:240px;
        background: #F3F4F5;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      img{
        width:275px;
        max-height: 240px;
        height:auto;
      }
    }
    .ques_detail{
      display: flex;
      flex-direction: row;
      margin:20px 0;
      span:first-child{
        min-width: 70px;
        font-size: 14px;
        color: #333333;
      }
      span:last-child{
        font-size: 14px;
        color: #687D97;
      }
    }
  }
  .orderComfirm{
    border: 1px solid #D8D8D8;
    padding:0 20px;
    height:560px;
    overflow-y: auto;
    p{
      font-size: 18px;
      color: #333333;
      margin: 20px 0;
    }
    .orderInfo{
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      div{
        width:50%;
        margin-bottom: 10px;
        span:first-child{
          font-size: 18px;
          color: #ADADAD;
        }
        span:last-child{
          margin-left: 10px;
        }
      }
    }
    ul{
      li{
        margin:0 auto;
        width:100%;
        height:60px;
        line-height: 60px;
        border-bottom: 1px solid #D8D8D8;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        font-size: 14px;
        color: #333333;
        div:first-child{
          display: flex;
          align-items: center;
        }
        div:last-child{
          color: #979797;
          font-size: 14px;
        }
      }
    }
  }
  .process_detaile{
    font-size: 14px;
    .detail_list{
      position: relative;
      &:before{
        width: 5px;
        height:5px;
        border-radius: 50%;
        content: '';
        background: #157AF7;
        position: absolute;
        top: 7px;
        left: -3px;
      }
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      padding-left: 20px;
      margin-left: 5px;
      div:first-child{
        font-size: 14px;
        color: #333333;
        display: flex;
        flex-direction: row;
      }
      div:last-child{
        font-size: 14px;
      }
    }
  }
</style>



